import listItem from '@/components/listItem.vue'
import Search from '@/components/index/search.vue'
import { mapState} from 'vuex'
import Time from '@/common/time.js'
export default {
	data(){
		return{
			list:[],
			limit:8,
			page:1
		}
	},
	components:{
		listItem,
		Search
	},
	computed:{
		...mapState({
			sessionKey:state=>state.sessionKey
		})
	},
	methods:{
		sortDesc(){
		},
		getDailyList(){
			uni.showLoading({
				title: '加载中...'
			});
			this.$api.classify({
				sessionKey:this.sessionKey,
				class:'daily',
				page:this.page,
				limit:this.limit
			}).then(res=>{
				this.list=res.goods;
				uni.hideLoading();
			})
		}			
	},
	onLoad(){
		this.getDailyList()
	}
}
